body {
  background: #000;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#start-text {
  word-spacing: .25em;
  text-align: center;
  color: #fff;
  vertical-align: middle;
  width: 100%;
  height: 10%;
  background: #000c;
  place-content: center;
  align-items: center;
  margin-top: 80%;
  font-family: AmericanCaptain;
  font-size: 36px;
  display: flex;
}

#idle-video, #start-video {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  z-index: -1;
  position: fixed;
  inset: 0;
}

@font-face {
  font-family: AmericanCaptain;
  src: url("../american_captain.6bc61fac.ttf");
}

#goal-video, #fail-video {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  z-index: -1;
  position: fixed;
  inset: 0;
}

#gjs-canvas {
  height: 45%;
  width: 15%;
  z-index: -1;
  background: none;
  margin-top: 50%;
}

#output_canvas {
  width: 100%;
  height: 50%;
}

.centered-container {
  width: 100%;
  height: 10%;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #ee2027;
  border-radius: 50%;
  animation: 2s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

